<template>
    <div>
        <div id="map" :style="MapStyle"></div>
    </div>
</template>

<script>
import bus from '../components/bus';
import {Map, View} from 'ol';
import TileLayer from 'ol/layer/Tile';
import OSM from 'ol/source/OSM';

export default {
    watch: {

    },
    data() {
        return {
            map: {},
            view: {},
            layers: [],

            MapStyle: {
                width: '100%',
                height: '500px',
            },
        }
    }, // data
    methods: {
        // 预定义地图要素（图层、视窗等）
        preDefine() {
            // view
            this.view = new View({
                center: [10955112.297939133,4828966.456559213],
                zoom: 4.5,
                projection: 'EPSG:3857',
            });
            // layers
            this.layers = [
                new TileLayer({
                    source: new OSM(),
                    opacity: 0.7
                }),
            ]
        },
        // 选中 HTML 元素，创建地图
        createMap() {
            this.map = new Map({
                target: 'map',
                view: this.view,
                layers: this.layers
            });

            this.map.on('click', event => {
                console.log('点击坐标：(' + event.coordinate.toString() + ')');
                console.log('地图缩放：' + event.map.getView().getZoom());
            });
        }
    }, // methods
    created() {
        const self = this;
        const HeaderHeight = 50;

        // 预设地图高度
        let MapHeight = window.innerHeight - HeaderHeight;
        self.MapStyle.height = MapHeight + 'px';

        // 定义地图高度监听事件
        bus.$off('mapResize');
        bus.$on('mapResize', win => {
            let MapHeight = win.innerHeight - HeaderHeight;
            self.MapStyle.height = MapHeight + 'px';
        });
    }, // created
    mounted() {
        // 创建地图
        this.preDefine();
        this.createMap();
    }, // mounted
}
</script>

